import React from "react";

const SearchBox = ({ searchQuery, setSearchQuery }) => {
  return (
    <input
      type="text"
      value={searchQuery}
      onChange={(e) => setSearchQuery(e.target.value)}
      placeholder="Search Astromon"
      style={{
        width: "100%",
        padding: "10px",
        marginBottom: "20px",
        borderRadius: "5px",
      }}
    />
  );
};

export default SearchBox;
