import React from "react";

const EvolutionIcons = () => {
  return (
    <div
      className="evolution-icons"
      style={{
        display: "flex",
        justifyContent: "space-between",
        margin: "20px 0",
      }}
    >
      {["evolution1", "evolution2", "evolution3", "evolution4"].map(
        (evolution, index) => (
          <img
            key={index}
            src={`/path-to-evolution-icons/${evolution}.png`}
            alt={`Evolution ${index + 1}`}
            style={{ width: "50px", height: "50px", cursor: "pointer" }}
            onClick={() => console.log(`Select Evolution ${index + 1}`)}
          />
        )
      )}
    </div>
  );
};

export default EvolutionIcons;
