import React from "react";

const SelectedAstromon = ({ selectedName }) => {
  return (
    selectedName && (
      <div className="selected-name">
        <img
          src={`/path-to-element-icon/${selectedName}.png`}
          alt="Element Icon"
          onClick={() => console.log("Change Element Functionality")}
          style={{
            cursor: "pointer",
            width: "30px",
            height: "30px",
            marginRight: "10px",
          }}
        />
        <span>{selectedName}</span>
      </div>
    )
  );
};

export default SelectedAstromon;
