import React from "react";

const SkillBoxes = () => {
  return (
    <div
      className="skill-boxes"
      style={{
        display: "flex",
        justifyContent: "space-between",
        marginTop: "20px",
      }}
    >
      {Array.from({ length: 4 }).map((_, index) => (
        <div
          key={index}
          style={{
            width: "50px",
            height: "50px",
            border: "1px solid #000",
            borderRadius: "5px",
            textAlign: "center",
            lineHeight: "50px",
          }}
          onClick={() => console.log(`Select Skill ${index + 1}`)}
        >
          Skill {index + 1}
        </div>
      ))}
    </div>
  );
};

export default SkillBoxes;
