import React from "react";

const StatsGrid = () => {
  return (
    <div
      className="stats-grid"
      style={{
        display: "grid",
        gridTemplateColumns: "repeat(2, 1fr)",
        gap: "10px",
      }}
    >
      <div>HP: 100</div>
      <div>ATK: 50</div>
      <div>DEF: 30</div>
      <div>REC: 20</div>
    </div>
  );
};

export default StatsGrid;
